import Web3 from 'web3';

import promisify from "../utils/promisify";
import abi from '../abis/abi.json';
import sd_abi from '../abis/sd_abi.json';
import locker_abi from '../abis/locker_abi.json';

import store from "../redux/store";
import {updateProjectLocker, updateProjectsMap} from "../redux/slices/divvySlice";

const web3 = new Web3('https://bsc-dataseed.binance.org/');

// const divvyContract = '0xe9490A983292bD844074Ac3e83c3cb408f578b61';
const fBNBContract = '0x87b1AccE6a1958E522233A737313C086551a5c76';
const deadWallet = '0x000000000000000000000000000000000000dead';
const divvyWallet = '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe';

const fBNBcontract = new web3.eth.Contract(abi, fBNBContract)

const getProjectStats = (project) => {

    const {contract, locker} = project;

    const c = new web3.eth.Contract(sd_abi, contract);

    //balance of fBNB in project
    const getFBNB = promisify(cb => fBNBcontract.methods.balanceOf(contract).call(cb));
    //balance of project in Liquidity
    const getLiquidity = promisify(cb => c.methods.getBalance(contract).call(cb));
    //balance of project in dead wallet
    const getBurnt = promisify(cb => c.methods.balanceOf(deadWallet).call(cb));
    //total supply
    const getSupply = promisify(cb => c.methods.totalSupply().call(cb));
    //asset backing
    const getBacking = promisify(cb => c.methods._totalSupply5().call(cb));
    //backing per token
    const getBackingPerToken = promisify(cb => c.methods.oneTokentoBacking().call(cb));
    //balance of tokens in Divvy Fund
    const getDivvyBalance = promisify(cb => c.methods.balanceOf(divvyWallet).call(cb));

    Promise.all([
        getFBNB,
        getLiquidity,
        getBurnt,
        getSupply,
        getBacking,
        getBackingPerToken,
        getDivvyBalance
    ]).then(resp => {
        const fbnb = resp[0] / Math.pow(10, 18) || 0;
        const backing = resp[4] / Math.pow(10, 18) || 0;
        const liquidity = resp[1] / Math.pow(10, 9) || 0;
        const supply = resp[3] / Math.pow(10, 9) || 0;
        const burnt = resp[2] / Math.pow(10, 9) || 0;
        const circulation = supply - burnt || 0;
        const price = (fbnb - backing) / liquidity || 0;

        store.dispatch(updateProjectsMap({
            contract: contract,
            stats: {
                fbnb,
                liquidity,
                burnt,
                supply,
                circulation,
                backing,
                backingPerToken: resp[5] / Math.pow(10, 18),
                divvyBalance: resp[6] / Math.pow(10, 9),
                price
            }
        }));
    });

    if(locker){
        const l = new web3.eth.Contract(locker_abi, locker);

        const availableReflections = promisify(cb => l.methods.availableReflections().call(cb));
        const lockedSupply = promisify(cb => l.methods.lockedSupply(contract).call(cb));
        const reaminingTime = promisify(cb => l.methods.reaminingTime().call(cb));

        Promise.all([
            availableReflections,
            lockedSupply,
            reaminingTime,
        ]).then(resp => {
            const reflections = resp[0] / Math.pow(10, 9) || 0;
            const locked = resp[1] / Math.pow(10, 9) || 0;
            const time = parseInt(resp[2]);

            store.dispatch(updateProjectLocker({
                contract: contract,
                locker: {
                    reflections,
                    locked,
                    time
                }
            }));
        });
    }

}

export default getProjectStats;