import axios from "axios";
import {useEffect, useState} from "react";
import {
    AppBar,
    createTheme,
    CssBaseline, Drawer,
    Grid, IconButton,
    ThemeProvider,
    Toolbar, Typography
} from "@mui/material";
import {
    HashRouter as Router,
    Routes,
    Route,
} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";

import ProjectList from "./components/ProjectList";
import ProjectDetails from "./views/ProjectDetails";
import {updateBNBPrice, updateProjects} from "./redux/slices/divvySlice";
import {useGetTickersByTypeQuery} from "./redux/services/BinanceTickerApi";
import ListIcon from '@mui/icons-material/List';
import Logo from './images/logo_small.png';
import getProjectStats from "./utils/projectInfo";

const theme = createTheme({
    palette: {
        primary: {
            main: '#872eff'
        }
    },
});

function DivvyDashboard() {

    const dispatch = useDispatch();
    const {bnbPrice, projects} = useSelector(state => state.divvy);
    const [open, setOpen] = useState(false);

    // get BNB price
    let binance = useGetTickersByTypeQuery('price?symbol=BNBUSDT', {
        pollingInterval: 30000,
    });

    useEffect(() => {
        if (binance.status === 'fulfilled') {
            dispatch(updateBNBPrice({price: binance.data.price}));
        }
    }, [binance.status, binance.data, dispatch])

    //get projects list
    useEffect(() => {

        const makeRequest = () => {
            axios
                .get('https://divvysd.com/dashboard/projects.json')
                .then(resp => {
                    dispatch(updateProjects(resp.data));

                    resp.data.map(project => {
                        getProjectStats(project);
                        return project;
                    })
                })
        }

        makeRequest();

        const interval = setInterval(makeRequest, 60000);
        return () => clearInterval(interval);
    }, [dispatch]);

    if (!projects.length || bnbPrice === 0) return <></>;

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <AppBar color="primary">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        aria-label="open drawer"
                        onClick={() => setOpen(true)}
                    >
                        <ListIcon/>
                    </IconButton>
                    <img alt="Divvy" src={Logo} height={32} style={{marginTop:5}}/>
                    <div style={{flexGrow: 1}}/>
                    <Typography>BNB ${bnbPrice.toFixed(2)}</Typography>
                </Toolbar>
            </AppBar>
            <Router>
                <Grid container style={{marginTop: 64}}>
                    <Drawer
                        open={open}
                        onClose={() => setOpen(false)}
                        PaperProps={{
                            style: {
                                width: 300
                            }
                        }}
                    >
                        <ProjectList projects={projects}
                                     onClick={() => setOpen(false)}/>
                    </Drawer>

                    <Grid item xs={12} style={{
                        height: 'calc(100% - 64px)',
                        overflow: 'auto',
                        padding: 20,
                        position: 'fixed',
                        backgroundColor: '#fafafa'
                    }}>
                        <Routes>
                            <Route
                                path={`/`}
                                exact={true}
                                element={<ProjectDetails project={projects[0]}/>}
                            />
                            {
                                projects.map((project, i) => {
                                    return <Route
                                        key={i}
                                        path={`/${project.symbol}`}
                                        exact={true}
                                        element={<ProjectDetails project={project}/>}
                                    />
                                })
                            }
                        </Routes>
                    </Grid>
                </Grid>
            </Router>
        </ThemeProvider>
    );
}

export default DivvyDashboard;