import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    bnbPrice: 0,
    projects: [],
    projectsMap: {}
};

const divvySlice = createSlice({
    name: 'divvy',
    initialState,
    reducers: {

        updateBNBPrice(state, action) {
            state.bnbPrice = parseFloat(action.payload.price);
        },

        updateProjects(state, action) {
            state.projects = action.payload;
        },

        updateProject(state, action) {
            state.projects.map( project => {
                if(project.contract === action.payload.id){
                    project.description = action.payload.description;
                    project.explorer = action.payload.explorer;
                    project.name = action.payload.name;
                    project.symbol = action.payload.symbol;
                    project.website = action.payload.website;
                }
                return project;
            })
        },

        updateProjectDetail(state, action) {
            state.projects.map( project => {
                if(project.contract === action.payload.contract){
                    project[action.payload.key] = action.payload.data;
                }
                return project;
            })
        },

        updateProjectsMap(state, action) {
            if(!state.projectsMap[action.payload.contract]){
                state.projectsMap[action.payload.contract] = {}
            }

            state.projectsMap[action.payload.contract].stats = action.payload.stats;
        },

        updateProjectLocker(state, action) {
            if(!state.projectsMap[action.payload.contract]){
                state.projectsMap[action.payload.contract] = {}
            }

            state.projectsMap[action.payload.contract].locker = action.payload.locker;
        }
    }
});

export const {
    updateBNBPrice,
    updateProjects,
    updateProject,
    updateProjectDetail,
    updateProjectsMap,
    updateProjectLocker
} = divvySlice.actions;

export default divvySlice.reducer;
